<template>
<div id="mine">
    <header-tab title="个人中心"/>
    <!-- <header v-show="$store.getters.isShow">
        <img :src="this.$store.getters.userTitle.userImg" alt="头像" />
        <h2 class="user-title">{{ this.$store.getters.userTitle.username }}</h2>
        <p>
            <span>登录状态:</span>
            在线 &nbsp;&nbsp;
            <a href="javascript:;" @click="logoutHandle">退出登录</a>
        </p>
    </header> -->
    <header>
        <img src="http://hiphotos.qianqian.com/ting/pic/item/eac4b74543a9822659d378408982b9014a90eb43.jpg" alt="pic" />
        <h2 class="user-title">{{ $store.state.user.name }} </h2>
        <div v-show="$store.state.user.name">
            <span style="color:red;" v-if="$store.state.user.isAdmin">超级管理员</span>
            <span style="color:blue;" v-else>普通会员</span>
        </div>
        <p>
            <span>登录状态:</span>
            在线 &nbsp;&nbsp;
            <a href="javascript:;" @click="handleLoginOut">退出登录</a>
        </p>
    </header>
    <!-- <div class="app-body">
        <textarea class="common-context" rows="4" v-model="commonContext"></textarea>
        <button type="button" class="common-btn" @click="sendCommonHandle">评论</button>
        <ul>
            <li v-for="item in commonList" :key="item.id">
                <img :src="item.img" />
                <div class="common-info">
                    <h3>{{ item.username }}</h3>
                    <p>{{ item.common }}</p>
                </div>
            </li>
        </ul>
    </div> -->
    <div class="app-body">
        <ul>
            <li v-for="item in 3" :key="item">
                <h3>{{ item}}</h3>
                <i class="fa fa-chevron-right"></i>
            </li>
        </ul>
    </div> 
    
    <footer class="login-box">
        <a href="javascript:;" @click="handleLogin">登录</a>
        <a href="javascript:;">注册</a>
    </footer>
    <!-- <footer class="login-box">
        <a @click="loginHandle">登录</a>
        <a @click="registerHandke">注册</a>
    </footer> -->
    <!-- <router-view /> -->
</div>
</template>

<script>
const HeaderTab = () => import("@/components/HeaderTab")
export default {
    name: "mine",
    data() {
        return {
            userInfo: {},
        }
    },
    components: {
        HeaderTab,
    },
    // 在渲染该组件的对应路由被 confirm 前调用
    // 不！能！获取组件实例 `this`
    // 因为当守卫执行前，组件实例还没被创建
    // 组件内的守卫（局部守卫）
    beforeRouteEnter (to, from, next) {
        if(to.path === "/mine") {
            next(vm => {
                // 获取用户信息
                const user = vm.$store.state.user.userList.filter(item => item.isLogin === true)[0] || '{}'

                localStorage.setItem("username", user.username)
                localStorage.setItem("isAdmin", user.isAdmin)

                vm.$store.commit("user/getUsername", {name: user.username,isAdmin: user.isAdmin})
            })
        }
    },
    methods: {
        // 退出
        handleLoginOut() {
            this.userInfo = this.$store.state.user.userList.filter(item => item.isLogin === true)[0]
            // console.log(this.userInfo)
            this.$store.commit("user/loginOut", this.userInfo)
            this.$router.go(0)
        },
        // 登录
        handleLogin() {
            let obj = this.$store.state.user.userList.filter(item => item.username === "admin")[0]
            this.$store.commit("user/login", obj)
            this.$router.go(0)
        }
    },
}
</script>

