	align-items: center;
	margin: 10px 8px 0;
	padding-bottom: 8px;
	border: 1px solid #eee;
	border-radius: 5px;
	background-color: #eee;
	box-shadow: 0 0 3px #999;
}
#mine header img {
	display: block;
	width: 100px;
	height: 100px;
	border-radius: 50%;
	margin-top: 10px;
}

#mine header .user-title {
	margin: 5px 0;
}

#mine header p {
	margin-top: 0;
	text-align: center;
}

.app-body {
	border: 1px solid #eee;
	background-color: #eee;
	box-shadow: 0 0 3px #999;
	border-radius: 5px;
	margin: 10px 8px 0;
	padding: 5px 15px;
}

.app-body .common-context {
	display: block;
	width: 100%;
	height: 80px;
	margin: 5px 0;
}

.app-body .common-btn {
	display: block;
	width: 100%;
	height: 45px;
	border: 0;
	outline: none;
	line-height: 45px;
	background-color: #22FF22;
	font-size: 18px;
	font-weight: 500;
}

.app-body .common-btn:active {
	background-color: #03BD03;
}

.app-body li {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;
	border-bottom: 1px solid #999;
	padding: 10px 8px;
}
.app-body li:last-child {
	border-bottom: 0;
}
.app-body li h3 {
	font-size: 16px;
	color: red;
}

#mine .login-box {
	display: flex;
	justify-content: space-between;
	height: 40px;
	line-height: 40px;
	background-color: #eee;
	box-shadow: 0 0 3px #999;
	border-radius: 5px;
	margin: 10px 8px;
	padding: 0 25px;
}
</style>